import { Icon } from '@material-ui/core';
import AburiOshi from './SVG/AburiOshi.svg';

//AburiOshi Icon
const AburiOshiIcon = () => {
    return (
        <Icon>
            <img alt='ab' src={AburiOshi} height={30} width={25} />
        </Icon>
    );
}


export default AburiOshiIcon
